@import "_reset";
@import "_variable";
@import "_mixins";
.ant-menu-item-disabled {
  cursor: default!important;
}
.cursor-unset:hover{
  cursor: default;
  color: #a2a2a2!important;
}
:root {
  --animate-delay: 3.5s;
}
.animated{
  transition: all .5s;
  -webkit-backface-visibility: hidden; // 
  visibility: hidden;
}
.animated.in{
  -webkit-backface-visibility: hidden; // 
  visibility: visible;
}
.ant-btn {
  background: #1C5FC2 !important;
}
$nav_height: 3.68vw;
$nav_width: 5.58vw;
.animated {
  animation-duration: 1s !important;
  animation-fill-mode: both;
}
.mod-header-fixed {
  position: fixed !important;
  top: 0;
  left: 0;
  background-color: rgb(1,16,67);
}
.mod-header {
  width: 100%;
  font-size: 0.7vw;
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  color: #fff;
  z-index: 100;
  transition: background 0.3s ease;
  background-size: cover;
/*  background: rgba(2, 30, 122,0.3);*/

  .layout {
    display: flex;
    min-width: 63.16vw;
    width: initial;
    height: 100%;
  }
  &-logo {
    display: block;
    width: 8.7vw;
    height: 2.2vw;
    background-image: url("../img/v2/logo.png");
    margin-left: 2.89vw;
    margin-top: 0.9vw;
    background-size: 100% 100%;
    background-position-y: center;
  }
  &.header-hover {
    background-color: rgba(1,18,74,.9);
    .mod-header-logo {
      background-image: url("../img/v2/active-logo.png") !important;
    }
    .try {
      background-color: #0f62fd;
      border: none;
    }
  }
  &-nav {
    height: 100%;
    #jmod-header-nav {
      overflow: hidden;
      height: 100%;
    }
    li {
      position: relative;
      float: left;
      line-height: 1;
    }
    a:hover {
      text-decoration: none;
    }
    .top-cat {
      text-align: center;
      // box-sizing: border-box;
      // width: $nav_width;
      padding: 0 0.42vw 0;
      margin-right: 2.11vw;
      height: 100%;
      line-height: 4.74vw;
      &:first-child {
        margin: 0 2.11vw 0 2.63vw;
      }
      h2 {
        position: relative;
        a {
          padding: 0;
          position: relative;
          font-size: 0.9vw;
          font-family: Microsoft YaHei;
          font-weight: 400;
          color: #fff;
          line-height: 1vw;
          display: inline-block;
          height: 100%;
          &:hover {
            color: #fff;
            &:after {
              content: "";
              position: absolute;
              display: block;
              height: 2px;
              width: 100%;
              bottom: -1.58vw;
              background-image: linear-gradient(90deg,
                      #0059ff 0%,
                      #2bffee 100%);
            }
          }
          &.active-route {
            color: #fff;
            &:after {
              content: "";
              position: absolute;
              display: block;
              height: 0.1vw;
              width: 100%;
              bottom: -0.7vw;
              background-image: linear-gradient(90deg,
                      #0059ff 0%,
                      #2bffee 100%);
            }
          }
        }
      }
      &-arrow {
        content: "";
        width: 20px;
        height: 12px;
        // @include sprite("slice/common/header-arrow-up.png");
        transition: transform 0.3s ease;
        transform: rotate(180deg);
        display: inline-block;
        vertical-align: bottom;
        margin-left: 4px;
      }
      &.hassub {
        &:hover,
        &:active {
          .case-menu {
            transform: scaleY(1);
          }
          .top-cat-arrow {
            transform: rotate(0deg);
          }
        }
      }
    }
    .slider {
      position: absolute;
      bottom: 0;
      left: 0;
      pointer-events: none;
      width: initial;
      height: 3px;
      background-color: #fff;
      transition: transform 0.3s ease-out, width 0.3s ease-out;
    }
  }
  .userRig {
    margin-right: 8.95vw;
  }
  &-user {
    position: absolute;
    right: 0;
    display: flex;
    align-items: center;
    height: 100%;
    .console {
      display: inline-block;
      padding: 0 8px;
      vertical-align: middle;
      font-size: 0.7vw;
      color: #fff;
      &:hover {
        color: #126ee1;
      }
    }
    .user {
      padding-left: 8px;
      display: inline-block;
      vertical-align: middle;
      position: relative;
      border-left: 1px solid #fff;
      font-size: 0.7vw;
      margin-right: 1vw;
      i {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        background: url('../../assets/img/v2/bottom.png') no-repeat;
        background-size: 100% 100%;
        width: 0.63vw;
        height: 0.84vw;
        margin-left: 0.42vw;
      }
      .dropdown {
        position: absolute;
        padding-top: 1vw;
        top: 1.21vw;
        left: 0;
        width: 100%;

        text-align: center;
        display: none;
        a {
          display: block;
          line-height: 2vw;
          background-color: #333333;
        }
      }
      &:hover {
        color: #126ee1;
        .dropdown {
          display: block;
        }
        a {
          color: #fff;
          &:hover {
            color: #126ee1;
          }
        }
      }
    }
    .try {
      color: #fff;
      //outline: 1px solid #fff;
      border: 1px solid #fff;
      border-radius: 0.15vw;
      padding: 0.42vw 0.95vw;
      //-moz-outline-radius: 0.15vw;
      line-height: 1;
      display: inline-block;
      transition: all 0.3s ease;
      vertical-align: middle;
      font-size: 0.7vw;
      margin-right: 1vw;
      margin-top: -0.13vw;
    }
    .try:hover {
      background-color: #0f62fd;
      border: none;
    }
  }
  &:hover,
  &.hover,
  &.active {
    .mod-header-logo {
      background-image: url("../img/v2/logo.png");
    }
    .mod-header {
      &-user {
        .user {
          i:after {
            border-top-color: #333;
          }
        }
      }
    }
  }
  .cat {
    position: relative;
    .layout {
      min-width: initial;
    }
    .ico-3 {
      display: inline-block;
      width: 1.95vw;
      height: 1.53vw;
      background: url("../../components/header/components/5g-overView/img/icon-3.png")
        no-repeat;
      background-size: contain;
      margin-right: 0.68vw;
    }
    .ico-1 {
      display: inline-block;
      width: 1.74vw;
      height: 1.68vw;
      background: url("../../components/header/components/5g-overView/img/icon-1.png")
        no-repeat;
        margin-right: 0.68vw;
      background-size: contain;
    }
    .ico-2 {
      display: inline-block;
      width: 1.89vw;
      height: 1.68vw;
      background: url("../../components/header/components/5g-overView/img/icon-2.png")
        no-repeat;
        margin-right: 0.68vw;
      background-size: contain;
    }
    .ico-4 {
      display: inline-block;
      width: 1.89vw;
      height: 1.68vw;
      background: url("../../components/header/components/5g-overView/img/icon-4.png")
        no-repeat;
        margin-right: 0.68vw;
      background-size: contain;
    }
    .ico-5 {
      display: inline-block;
      width: 1.89vw;
      height: 1.68vw;
      background: url("../../components/header/components/5g-overView/img/icon-5.png")
        no-repeat;
        margin-right: 0.68vw;
      background-size: contain;
    }
    .ico-6 {
      display: inline-block;
      width: 1.89vw;
      height: 1.68vw;
      background: url("../../components/header/components/5g-overView/img/icon-6.png")
        no-repeat;
        margin-right: 0.68vw;
      background-size: contain;
    }
    .ico-7 {
      display: inline-block;
      width: 1.89vw;
      height: 1.68vw;
      background: url("../../components/header/components/5g-overView/img/icon-7.png")
        no-repeat;
        margin-right: 0.68vw;
      background-size: contain;
    }
    .ico-8 {
      display: inline-block;
      width: 1.89vw;
      height: 1.68vw;
      background: url("../../components/header/components/5g-overView/img/icon-8.png")
        no-repeat;
        margin-right: 0.68vw;
      background-size: contain;
    }
    .ico-9 {
      display: inline-block;
      width: 1.89vw;
      height: 1.68vw;
      background: url("../../components/header/components/5g-overView/img/icon-9.png")
        no-repeat;
        margin-right: 0.68vw;
      background-size: contain;
    }
    .ico-10 {
      display: inline-block;
      width: 1.89vw;
      height: 1.68vw;
      background: url("../../components/header/components/5g-overView/img/icon-10.png")
        no-repeat;
        margin-right: 0.68vw;
      background-size: contain;
    }
    &-tag {
      display: inline-block;
      vertical-align: middle;
      width: 28px;
      height: 12px;
      margin-top: -2px;
    }
    &-box {
      color: #a2a2a2;
      /*background: #f3f4f5;*/
      background-color: rgba(1,18,74,.95);
      box-sizing: border-box;
      position: absolute;
      width: 100%;
      top: 4.05vw;
      height: 25.7vw;
      left: 0;
      text-align: left;
      transition: transform 0.3s ease-out;
      transform: scaleY(0);
      transform-origin: center top;
      padding: 0 0 0vw 17.3vw;
      &:hover,
      &.active {
        transform: scaleY(1);
      }
      .layout {
        padding-top: 1.2vw;
      }
    }
    &-col {
      width: 56vw;
      display: flex;
      flex-wrap: wrap;
      // display: inline-block;
      // vertical-align: top;
      // width: 280px;
      // margin-right: 27px;
      // padding-right: 75px;
      // box-sizing: border-box;
      &.more-margin {
        .cat-group.top-row {
          margin-bottom: 3vw;
        }
      }
    }
    &-group {
      margin: 1.11vw 0;
      &.top-row {
        margin: 0 0 0 0;
      }
      &.bottom-row {
        margin-bottom: 1.32vw;
      }
    }
    .no-marginTop {
      margin-top: 1.84vw;
    }
    &-tit {
      width: 8.89vw;
      font-size: 0.8vw;
      font-family: Microsoft YaHei;
      font-weight: 400;
      color: #fff;
      line-height: 1.84vw;
      padding-bottom: 0vw;
      margin-bottom: 0;
      border-bottom: 1px solid rgba(0,81,255,.5);
      padding-right: 0vw;
      margin-right: 5vw;
      i {
        display: none!important;
      }
    }
    &-item {
      margin-bottom: -0.4vw;
      a {
        height: 0.84vw;
        font-size: 0.7vw;
        font-family:Microsoft YaHei;
        font-weight:400;
        color:rgba(162,162,162,1);
        line-height: 2.26vw;
      }
      a:hover {
        color: #126ee1;
      }
    }
  }
}
