.left-align {
  text-align: left;
}

.company-attachment {
  display: flex;
}
.step-bg{
  width: 100%;
  height: 100vh;
  position: relative;
  canvas{
    width: 100%;
    height: 100%!important;
  }
  background: #011043;
  overflow: hidden;
  //background: url("../../assets/img/login/BG.jpg") no-repeat;
  //background-size: 100% 100%;
  .ant-tabs-content{
    margin-top: 0!important;
  }

}
.step-wrapper {
  margin: 0!important;
  .leftSide {
    .gif-img {
      width: 69.8vw;
      height: 30.5vw;
      position: absolute;
      top: 19vw;
      left: -1.8vw;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .text-img {
      top:15vw;
      left:27vw;
      width: 17.7vw;
      height: 5.2vw;
      position: absolute;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
.logo-gif{
  width: 37.8vw;
  height: 30.5vw;
}
  position: relative;
  .header-logo {
    position: absolute;
    width: 8.7vw;
    height: 2.2vw;
    left:2.89vw;
    top: 0.9vw;
    background-image: url("../../assets/img/v2/logo.png");
    background-size: 100%;
    background-position-y: center;
    a {
      display: inline-block;
      width: 100%;
      height: 100%;
    }
  }
  .login-left {
    width: 1318px;
    height: 1080px;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    .login-content {
      h1 {
        font-size: 24px;
        font-family: Microsoft YaHei;
        font-weight: bold;
        color: rgba(28, 95, 194, 1);
        line-height: 35px;
        display: flex;
        align-items: center;
        margin-bottom: 22px;
        .line {
          display: inline-block;
          width: 2px;
          height: 26px;
          background: rgba(31, 105, 200, 1);
          margin-right: 7px;
        }
      }
      p {
        width: 600px;
        margin-top: 24px;
        font-family: MicrosoftYaHei;
        font-size: 16px;
        font-weight: normal;
        font-stretch: normal;
        line-height: 28px;
        letter-spacing: 0px;
        color: #808080;
        text-align: left;
      }
    }
  }
  .login-right {
    //width: 602px;
    //height: 1080px;
    background-size: cover;
    position: relative;
  }
  .login-box {
    width: 28.1vw;
    height: 35.4vw;
    background-color: rgba(15, 98, 253, .1);
    position: absolute;
    right: 9.9vw;
    top: 11.3vw;
    //transform: translate(-50%, -50%);
    //box-shadow: 0px 0px 8px 0px rgba(94, 94, 133, 0.2);
    border-radius: 3px;
    //padding: 28px 37px 0px;
    .icon-logo {
      display: inline-block;
      width: 4.9vw;
      height: 4.9vw;
      margin: 2vw 0 1.8vw 0;
      background-image: url('../../assets/img/login/LOGO2.png');
      background-size: 100% 100%;
    }
    .ant-tabs {
      .ant-tabs-nav .ant-tabs-tab {
        font-family: MicrosoftYaHei;
        font-size: 1.3vw;
        font-weight: normal;
        font-stretch: normal;
        line-height: 28px;
        letter-spacing: 0px;
        color: #fff;
        margin-bottom: 1vw;
      }
      .mobile-no {
        flex: 1;
        max-width: 100%;
        height: 37px;
      }
      .mobile-no-pass {
        flex: 0.95;
        max-width: 100%;
      }
    }
    .ant-input {
      height: 37px;
    }
    .btn-top {
      margin-top: 40px;
    }
    .ant-btn {
      width: 202px;
      height: 37px;
      border-radius: 3px;
    }
    .ant-col-4 {
      max-width: 24.666667%;
      margin-right: 16px;
    }
    .maigin-Top {
      margin-top: 40px;
    }
    .impowerBox .qrcode {
      width: 200px;
      height: 200px;
    }

  }
  .mobile-no {
    display: flex;
    margin: 10px;
    span {
      line-height: 32px;
      width: 100px;
    }
  }
  .ant-form-item-control-input-content {
    display: flex;
    justify-content: space-between;
  }
  .ant-form-item-control {
    flex-grow: 1 !important;
  }
  .ant-col-12 {
    flex: 1 1;
    max-width: 100%;
  }
  .form-btn {
    .ant-form-item-control-input-content {
      justify-content: center;
    }
  }
  .ant-btn[disabled] {
    color: #fff;
    border: 0;
    &:hover {
      color: #fff;
    }
  }
  .box-sms {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
  }
  .msg-btn-container button {
    margin-left: 15px;
  }
  .ant-col-16 {
    max-width: 100%;
  }
}
#login_container1{
  display: flex;
  align-items: center;
  justify-content: center;
}
.impowerBox .qrcode {width: 15.1vw;margin-top: 28px;}
.impowerBox .title {display: none;}
.impowerBox .info {width: 200px; margin-top: 14px;}
